<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right" class="el-breadcrumb">
      <el-breadcrumb-item :to="{path:'/main'}">首页</el-breadcrumb-item>
      <el-breadcrumb-item>重要信息管理</el-breadcrumb-item>
      <el-breadcrumb-item>员工管理</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card class="el-card">

      <el-row :gutter="10">
        <el-col :span="8">
          <el-input placeholder="请输入管理员名称" v-model="userName">
            <el-button slot="append" icon="el-icon-search" @click="handleSearch"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible=true">添加信息</el-button>
        </el-col>
      </el-row>

      <el-table :data="tableData">
        <el-table-column prop="id" label="序号" width="70"></el-table-column>
        <el-table-column prop="userName" label="账号" width="100"></el-table-column>
        <el-table-column prop="userPassword" label="密码" width="120"></el-table-column>
        <el-table-column prop="userSex" label="性别" width="100"></el-table-column>
        <el-table-column prop="userPhone" label="电话号码" width="140"></el-table-column>
        <el-table-column prop="userEmail" label="电子邮箱" width="180"></el-table-column>
        <el-table-column label="操作" min-width="250">
          <template slot-scope="scope">
            <el-button type="primary" @click="showReadDialog(scope.row.id)" icon="el-icon-reading">查看</el-button>
            <el-button type="warning" @click="showEditDialog(scope.row.id)" icon="el-icon-edit">修改</el-button>
            <el-button type="danger" icon="el-icon-delete" @click="removeUserById(scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="pageSize"
        layout="total,prev, pager, next"
        :total="total">
      </el-pagination>
    </el-card>

    <el-dialog
      title="添加管理员信息"
      :visible.sync="addDialogVisible"
      width="40%"
    >
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="110px" @close="addDialogClosed">
        <el-form-item label="管理员账号" prop="userName">
          <el-input v-model="addForm.userName"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="userPassword">
          <el-input v-model="addForm.userPassword"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="userSex">
          <el-input v-model="addForm.userSex"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" prop="userPhone">
          <el-input v-model="addForm.userPhone"></el-input>
        </el-form-item>
        <el-form-item label="电子邮箱" prop="userEmail">
          <el-input v-model="addForm.userEmail"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addUser">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="修改信息"
      :visible.sync="editDialogVisible"
      width="40%"
    >
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="110px" @close="editDialogClosed">
        <el-form-item label="管理员账号" prop="userName">
          <el-input v-model="editForm.userName"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="userPassword">
          <el-input v-model="editForm.userPassword"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="userSex">
          <el-input v-model="editForm.userSex"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" prop="userPhone">
          <el-input v-model="editForm.userPhone"></el-input>
        </el-form-item>
        <el-form-item label="电子邮箱" prop="userEmail">
          <el-input v-model="editForm.userEmail"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editUser">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="查看"
      :visible.sync="readDialogVisible"
      width="40%"
    >
      <el-form :model="readForm" ref="readFormRef" label-width="90px" >
        <el-form-item label="管理员账号" prop="userName">
          <el-input v-model="readForm.userName" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="userPassword">
          <el-input v-model="readForm.userPassword" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="userSex">
          <el-input v-model="readForm.userSex" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" prop="userPhone">
          <el-input v-model="readForm.userPhone" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="电子邮箱" prop="userEmail">
          <el-input v-model="readForm.userEmail" :disabled="true"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="readDialogVisible = false">关  闭</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
    export default {
        name: "userManage",
      data() {
        return {
          pageSize: 8,
          currentPage: 1,
          tableData: {
            id: "",
            userName: "",
            userPassword: "",
            userSex: "",
            userPhone: "",
            userEmail: ""
          },
          total: 0,
          userName: "",
          addDialogVisible: false,
          addForm: {
            id: "",
            userName: "",
            userPassword: "",
            userSex: "",
            userPhone: "",
            userEmail: ""
          },
          addFormRules: {
            userName: [
              {required: true, message: '不可为空', trigger: 'blur'},
              {min: 1, max: 20, message: '字符长度在2~20个字符之间', trigger: 'blur'}
            ],
            userPassword: [
              {required: true, message: '不可为空', trigger: 'blur'}
            ],
            userSex: [
              {required: true, message: '不可为空', trigger: 'blur'}
            ],
            userPhone: [
              {required: true, message: '不可为空', trigger: 'blur'}
            ],
            userEmail: [
              {required: true, message: '不可为空', trigger: 'blur'}
            ]
          },
          editDialogVisible:false,
          editForm: {
            id: "",
            userName: "",
            userPassword: "",
            userSex: "",
            userPhone: "",
            userEmail: ""
          },
          editFormRules: {
            userName: [
              {required: true, message: '不可为空', trigger: 'blur'},
              {min: 1, max: 20, message: '字符长度在2~20个字符之间', trigger: 'blur'}
            ],
            userPassword: [
              {required: true, message: '不可为空', trigger: 'blur'}
            ],
            userSex: [
              {required: true, message: '不可为空', trigger: 'blur'}
            ],
            userPhone: [
              {required: true, message: '不可为空', trigger: 'blur'}
            ],
            userEmail: [
              {required: true, message: '不可为空', trigger: 'blur'}
            ]
          },
          readDialogVisible:false,
          readForm:{
            id: "",
            userName: "",
            userPassword: "",
            userSex: "",
            userPhone: "",
            userEmail: ""
          },
        }
      },
      created() {
        this.queryUserList()
      },
      methods: {
        queryUserList() {
          this.$http.get('user/queryUser',
            {params: {currentPage: this.currentPage, pageSize: this.pageSize, userName: this.userName}})
            .then(res => {
              this.total = res.data.total;
              this.tableData = res.data.data;
            });
        },
        handleCurrentChange(currentPage) {
          this.currentPage = currentPage
          this.queryUserList()
        },
        handleSearch() {
          this.currentPage = 1
          this.queryUserList()
        },
        addDialogClosed(){
          this.$refs.addFormRef.resetFields()
        },
        addUser(){
          this.$refs.addFormRef.validate(async valid=>{
            if(!valid) return
            const {data:res}=await this.$http.post("user/addUser",this.addForm)
            if(res.code!==200){
              this.$message.error("添加信息失败,请检查信息")
            }else {
              this.$message.success("添加成功")
              this.addDialogVisible=false
              this.queryUserList()
            }
          })
        },
        showEditDialog(id){
          this.editDialogVisible =true
          this.$http.get('user/getUserById',{params:{id:id}})
            .then(res => {
              this.editForm = res.data;
            });
        },
        editDialogClosed(){
          this.$refs.editFormRef.resetFields()
        },
        editUser(){
          this.$refs.editFormRef.validate(async valid =>{
            if(!valid) return
            const {data:res}=await this.$http.post("user/editUser",this.editForm)
            if(res.code!==200){
              this.$message.error("修改信息失败,请检查信息")
            }else {
              this.$message.success("修改信息成功")
              this.editDialogVisible=false
              this.queryUserList()
            }
          })
        },
        removeUserById(id){
          this.$confirm('此操作将永久删除, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.$http.get('user/removeUserById',{params:{id:id}})
            this.queryUserList()
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        },
        showReadDialog(id){
          this.readDialogVisible =true
          this.$http.get('user/getUserById',{params:{id:id}})
            .then(res => {
              this.readForm = res.data;
            });
        },
      }
    }
</script>

<style scoped lang="scss">
  .el-breadcrumb {
    margin-bottom: 15px;
    font-size: 17px;
  }

  .el-card {
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
  }
</style>



